import Vue from 'vue'
import Router from 'vue-router'
import {asyncLoad, page, parentRoute} from '@/utils/fun'
import uiRoutes from './ui'

Vue.use(Router)

let demoRoutes = [
  {
    path: 'vuex-demo',
    name: 'vuex-demo',
    component: page('demo/vuex')
  },
  {
    path: 'page1',
    name: 'page1',
    component: page('demo/page1')
  },
  {
    path: 'page2',
    name: 'page2',
    component: page('demo/page2')
  }
]



export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'hello',
      component: asyncLoad('hello')
    },
    {
      path: '/test',
      name: 'test',
      component: asyncLoad('test')
    },
    {
      path: '/admin',
      name: 'admin',
      component: page('demo/admin')
    },
    {
      path: '/apitest',
      name: 'api',
      component: page('demo/apitest')
    }
  ].concat(
    parentRoute('/demo', demoRoutes),
    parentRoute('/ui', uiRoutes)
  ).concat(
    {
      path: '*',
      name: '404',
      component: page('common/404')
    }
  )
})



